<template>
    <div class="t-textbox">
        <!-- 标题 -->
        <h4 style="margin-bottom:10px"><span style="color: red;" v-if="data1.templateRequire === 1">*</span>{{index}}.{{data1.templateTitle}}</h4>
        <!-- 描述 -->
        <p class="t-desc">{{data1.templateDesc}}</p>
        <!-- 普通输入框 -->
        <el-input
            v-if="data1.templateType==='normal'"
            placeholder="请输入内容"
            v-model="data1.data"
            size="small"
            style="width:50%">
            <i slot="prefix" :class="'iconfont '+data.templateIcon" style="line-height:32px;margin-left:5px"></i>
        </el-input>
        <!-- 区域输入框 -->
        <el-input
            v-else-if="data1.templateType==='textarea'"
            type="textarea"
            placeholder="请输入内容"
            v-model="data1.data"
            size="small"
            style="width:95%">
        </el-input>
        <!-- 汉字输入框 -->
        <el-input
            v-else-if="data1.templateType==='Chinese'"
            placeholder="请输入内容"
            v-model="data1.data"
            size="small"
            @input="limitInputChinese"
            style="width:50%">
            <i slot="prefix" :class="'iconfont '+data.templateIcon" style="line-height:32px;margin-left:5px"></i>
        </el-input>
        <!-- 数字输入框 -->
        <el-input
            v-else-if="data1.templateType==='number'"
            placeholder="请输入内容"
            v-model="data1.data"
            size="small"
            @input="limitInputNumber"
            style="width:50%">
            <i slot="prefix" :class="'iconfont '+data.templateIcon" style="line-height:32px;margin-left:5px"></i>
        </el-input>
    </div>
</template>
<script>
export default {
    data:function(){
        return{
            data1:this.data
        }
    },
    props:{
        data:{
            type:Object,
            // default:function(){
            //     return{
            //         title:'未命名',
            //         desc:'描述',
            //         type:'normal',
            //         data:''
            //     }
            // }
        },
        index:Number
    },
    methods:{
        // 限制输入数字
        limitInputNumber(){ 
            this.data1.data = this.data1.data.replace(/[^\d]/g, '')
        },
        // 限制输入汉字 
        limitInputChinese(){
            // this.data1.data = this.data1.data.replace(/[^\u4E00-\u9FA5]/g, '')
        }
    }
}
</script>