<template>
    <div class="t-select">
        <!-- 标题 -->
        <h4 style="margin-bottom:10px"><span style="color: red;" v-if="data1.templateRequire === 1">*</span>{{index}}.{{data1.templateTitle}}</h4>
        <!-- 描述 -->
        <p class="t-desc">{{data1.templateDesc}}</p>
        <!-- 下拉选择框 -->
        <el-select v-model="data1.data" filterable placeholder="请选择" size="small">
            <el-option
                v-for="(item,index) in data1.templateContent"
                :key="index"
                :label="item.value"
                :value="item">
                {{item.value}}
            </el-option>
        </el-select>
    </div>
</template>
<script>
export default {
    data:function(){
        return{
            data1:this.data
        }
    },
    props:{
        data:{
            type:Object
        },
        index:Number
    }
}
</script>
