export default {
    //添加填空题
    textbox_add(title, desc, icon, type) {
        return {
            selected: false,
            templateContent: null,
            templateContentDefault: null,
            templateCount: null,
            templateDefault: "自定",
            templateDesc: desc,
            templateId: 2,
            templateName: "textbox",
            templateTitle: title,
            templateType: type,
            templateIcon: icon,
            templateImgUrl: null,
            templateRequire:0,
            data: ''
        }
    },
    //添加标题
    head_add(head, detailed) {
        return {
            selected: false,
            templateContent: null,
            templateContentDefault: null,
            templateCount: null,
            templateDefault: "自定",
            templateDesc: detailed,
            templateId: 3,
            templateName: "head",
            templateTitle: head,
            templateType: null,
            templateImgUrl: null,
            data: ''
        }
    },
    //添加单选框
    radio_add(title, desc, options) {
        return {
            selected: false,
            templateContent: options,
            templateContentDefault: null,
            templateCount: null,
            templateDefault: "自定",
            templateDesc: desc,
            templateId: 0,
            templateName: "radio",
            templateTitle: title,
            templateType: null,
            templateIcon: null,
            templateImgUrl: null,
            templateRequire:0,
            data: ''
        }
    },
    //添加多选框
    checkbox_add(title, desc, options) {
        return {
            selected: false,
            templateContent: options,
            templateContentDefault: null,
            templateCount: null,
            templateDefault: "自定",
            templateDesc: desc,
            templateId: 1,
            templateName: "checkbox",
            templateTitle: title,
            templateType: null,
            templateIcon: null,
            templateImgUrl: null,
            templateRequire:0,
            data: []
        }
    },
    //添加下拉选择框
    select_add(title, desc, options) {
        return {
            selected: false,
            templateContent: options,
            templateContentDefault: null,
            templateCount: null,
            templateDefault: "自定",
            templateDesc: desc,
            templateId: 4,
            templateName: "select",
            templateTitle: title,
            templateType: null,
            templateIcon: null,
            templateImgUrl: null,
            templateRequire:0,
            data: ''
        }
    },
    //添加图片单选题
    picradio_add(title, desc, options) {
        return {
            selected: false,
            templateContent: options,
            templateContentDefault: null,
            templateCount: null,
            templateDefault: "自定",
            templateDesc: desc,
            templateId: 5,
            templateName: "picradio",
            templateTitle: title,
            templateType: null,
            templateIcon: null,
            templateImgUrl: [],
            templateRequire:0,
            data: ''
        }
    },
    //添加图片多选
    piccheckbox_add(title, desc, options) {
        return {
            selected: false,
            templateContent: options,
            templateContentDefault: null,
            templateCount: null,
            templateDefault: "自定",
            templateDesc: desc,
            templateId: 6,
            templateName: "piccheckbox",
            templateTitle: title,
            templateType: null,
            templateIcon: null,
            templateImgUrl: [],
            templateRequire:0,
            data: []
        }
    },
    add_table_comp(data) {//传入组件名
        switch (data.comp) {
            case 'radio':
                return this.radio_add(data.title, '', data.options)
            case 'checkbox':
                return this.checkbox_add(data.title, '', data.options)
            case 'select':
                return this.select_add(data.title, '', data.options)
            case 'textbox':
                return this.textbox_add(data.title, '', data.icon, data.type)
            case 'picradio':
                return this.picradio_add(data.title, '', data.options)
            case 'piccheckbox':
                return this.piccheckbox_add(data.title, '', data.options)
        }
    },
    // 编辑框组件判断
    is_editpage_comp(component) {
        switch (component) {
            case 'radio':
                return 'e-radio'
            case 'checkbox':
                return 'e-checkbox'
            case 'select':
                return 'e-select'
            case 'textbox':
                return 'e-textbox'
            case 'head':
                return 'e-head'
            case 'picradio':
                return 'e-picradio'
            case 'piccheckbox':
                return 'e-piccheckbox'
        }
    },
    // 展示框组件判断
    is_table_comp(component) {
        switch (component) {
            case 'radio':
                return 't-radio'
            case 'checkbox':
                return 't-checkbox'
            case 'select':
                return 't-select'
            case 'textbox':
                return 't-textbox'
            case 'head':
                return 't-head'
            case 'picradio':
                return 't-picradio'
            case 'piccheckbox':
                return 't-piccheckbox'
        }
    },
    // 新增选项并插空方法，具体用于组件Edit页面
    getNumBycircluerSort(dataList) {
        console.log(dataList)
        let flag = false
        let num ;
        if (dataList.length !== 0) {
            for (let i=0;i<dataList.length;i++) {
                flag = false
                for (let r=0;r<dataList.length;r++) {
                    if (dataList[r].key === i) {
                        flag = true
                        break
                    }
                }
                if (!flag) {
                    num = i
                    flag = false
                    break
                }
            }
            if (flag) {
                num = dataList.length
            }
        } else {
            num = 0
        }
        return Number(num)
    },
    //提交表单
    commitForm(form){
      let submit_format = {
        dataWriteTime: 10,
        form: {
          formId: form.formId,
        },
        dataDetailsList: [],
      };
      for (let r in form.templateList) {
        let templateList = {
          dataContent: JSON.stringify(form.templateList[r].data),
          formTemplateId: form.templateList[r].formTemplateId,
        };
        if (typeof form.templateList[r].data === "string") {
          templateList.dataContent = form.templateList[r].data;
        } else if (typeof form.templateList[r].data === "object") {
          templateList.dataContent = JSON.stringify(
            form.templateList[r].data
          );
        }
        submit_format.dataDetailsList.push(templateList);
      }
      return submit_format
    },
    //检查是否必填已填
    checkRequired(form){
      for(let r in form.templateList){
        if((form.templateList[r].data === "" || form.templateList[r].data === undefined || form.templateList[r].data === null || form.templateList[r].data === [] || form.templateList[r].data.length === 0) && form.templateList[r].templateRequire === 1){
          return false
        }
      }
      return true
    }
}
